<template>
  <div class="monster-compass">
    <div v-if="isShowCompass" class="compass-box">
      <!-- 林開郡 start -->
      <!-- <div @click="showluluMap" class="compass-ling">showluluMap</div> -->
      <!-- 林開郡 end -->
      <qrcode-stream class="compass-scan" @decode="onDecode" @init="onInit" />
      <img class='compass-logo' src='@/assets/images/compass-scan.png' alt='scan'>
      <div class="compass-head">追蹤器</div>
      <div class="compass-text">請使用地圖 QRcode 前往怪奇地圖</div>
      <p class="error">{{ error }}</p>
      <p class="decode-result"><b>{{ result }}</b></p>
    </div>
  </div>
</template>

<script>

import { QrcodeStream } from 'vue-qrcode-reader'

export default {
  name: 'MonsterCompass',
  components: {
    QrcodeStream
  },
  data () {
    return {
      isShowCompass: true,
      isShowBackMap: false,
      result: '',
      error: ''
    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    showLingMap () {
      if (this.isOnlyFightLing) {
        this.$router.push({
          path: './haunted-house-fight'
        })
      } else {
        this.$router.push({
          path: './haunted-house-compass'
        })
      }
    },
    showluluMap () {

    },
    showKimMap () {
      if (this.isOnlyFightKim) {
        this.$router.push({
          path: './commute-kim-hauntedhouse-fight'
        })
      } else {
        this.$router.push({
          path: './commute-arrive-position-point'
        })
      }
    },
    onDecode (result) {
      this.result = result
      switch (result) {
        case 'lin':
          if (this.isLingWin) {
            alert('林開郡線已通關！')
          } else {
            this.showLingMap()
          }
          break
        case 'lulu':
          if (this.isLuluWin) {
            alert('露露線已通關！')
          } else {
            if (this.isOnlyFightLulu) {
              this.$router.push({
                path: './lulu-house-fight'
              })
            } else {
              this.$router.push({
                path: './lulu-house-compass'
              })
            }
          }
          break
        case 'kim':
          if (this.isKimWin) {
            alert('通勤線已通關！')
          } else {
            this.showKimMap()
          }
          break
        case 'river':
          if (this.isRiverWin) {
            alert('旭川線已通關！')
          } else {
            if (this.isOnlyFightRiver) {
              this.$router.push({
                path: './river-house-fight'
              })
            } else {
              this.$router.push({
                path: './river-house-compass'
              })
            }
          }
          break
        default:
          console.log('Unknow')
      }
    },
    async onInit (promise) {
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          this.error = 'ERROR: you need to grant camera access permisson'
        } else if (error.name === 'NotFoundError') {
          this.error = 'ERROR: no camera on this device'
        } else if (error.name === 'NotSupportedError') {
          this.error = 'ERROR: secure context required (HTTPS, localhost)'
        } else if (error.name === 'NotReadableError') {
          this.error = 'ERROR: is the camera already in use?'
        } else if (error.name === 'OverconstrainedError') {
          this.error = 'ERROR: installed cameras are not suitable'
        } else if (error.name === 'StreamApiNotSupportedError') {
          this.error = 'ERROR: Stream API is not supported in this browser'
        }
      }
    }
  },
  computed: {
    isLingWin () {
      return this.$store.state.isLingWin
    },
    isKimWin () {
      return this.$store.state.isKimWin
    },
    isLuluWin () {
      return this.$store.state.isLuluWin
    },
    isRiverWin () {
      return this.$store.state.isRiverWin
    },
    isOnlyFightLing () {
      return this.$store.state.isOnlyFightLing
    },
    isOnlyFightLulu () {
      return this.$store.state.isOnlyFightLulu
    },
    isOnlyFightKim () {
      return this.$store.state.isOnlyFightKim
    },
    isOnlyFightRiver () {
      return this.$store.state.isOnlyFightRiver
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .compass {

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
      }

      &-scan {
        position: absolute;
        top: 5%;
        width: 55%;
        height: 55%;
      }

      &-ling {
        color: #fff;
        font-size: 30px;
        margin-top: 20px;
      }

      &-logo {
        width: 200px;
      }

      &-head {
        font-size: 20px;
        color: #95ff00;
        margin-top: 10px;
      }

      &-text {
        color: #fff;
        margin-top: 4px;
      }
    }
  }

.error {
  font-weight: bold;
  color: red;
}

.decode-result {
  display: none !important;
}

</style>



